.quiz{
  padding: 40px 75px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.overlay--yellow{
    position: absolute;
    top: 0px;
    right: 0px;
}
.overlay--blue{
    position: absolute;
    bottom: 0px;
    left: 0px;
}

.game-over{
  display: flex;
  margin-top: 20px;
  p{
      font-family: 'Inter';
      font-weight: 700;
      font-size: 16px;
      color: #293264;
      margin-right: 25px;
  }
}
.check-answers{
  margin-top: 20px;
}